.business-enquiry-container .popup:before {
  background: linear-gradient(150.14deg, #6480EB 0.6%, #433BC4 100%);
  opacity: 0.9; }

.business-enquiry-modal {
  max-width: 458px !important; }
  .business-enquiry-modal .modal-header {
    padding-bottom: 0;
    padding-left: 0; }
  .business-enquiry-modal .modal-body {
    padding-left: 0;
    padding-right: 0; }
  .business-enquiry-modal .modal-footer button {
    margin: auto; }
  .business-enquiry-modal .checkbox-input-desc {
    color: #6E7F95;
    font-size: 12px;
    font-weight: normal;
    transform: translateY(3px); }
