.detail-collapse-container {
  margin-top: 30px;
  padding: 0; }
  .detail-collapse-container .collapse-button {
    width: 100%;
    background: white;
    box-shadow: 0px -2px 4px rgba(0, 0, 0, 0.05);
    border: none;
    display: flex;
    justify-content: center;
    align-items: center; }
    .detail-collapse-container .collapse-button .collapse-text {
      font-weight: bold;
      font-size: 10px;
      color: #6E7F95;
      margin: 0 13px 0 0; }
    .detail-collapse-container .collapse-button .rotate-arrow {
      transform: rotate(180deg); }
    .detail-collapse-container .collapse-button img {
      width: 10px;
      height: auto;
      filter: brightness(0) saturate(100%) invert(78%) sepia(45%) saturate(5238%) hue-rotate(121deg) brightness(96%) contrast(101%);
      transition: transform 0.3s ease; }
  .detail-collapse-container hr {
    margin-top: 0 !important; }
