.business-main-container .margin-padding {
  max-width: 1110px;
  padding: 0 40px;
  margin: auto; }
  @media screen and (max-width: 991px) {
    .business-main-container .margin-padding {
      padding: 0 15px; } }

.business-main-container .carousel-container {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media screen and (max-width: 991px) {
    .business-main-container .carousel-container {
      margin-bottom: 20px; } }
  .business-main-container .carousel-container .insurer-carousel-label {
    color: #2C3652;
    font-size: 14px;
    font-weight: bold;
    margin-bottom: -20px; }
  .business-main-container .carousel-container .insurer-logo-carousel {
    max-width: 650px;
    width: 100%;
    display: flex;
    justify-content: center;
    transform: translateY(-30px); }
    .business-main-container .carousel-container .insurer-logo-carousel .logo {
      height: 50px;
      display: flex; }
      .business-main-container .carousel-container .insurer-logo-carousel .logo img {
        margin: auto; }

.business-main-container .text-link {
  margin-left: 3px;
  font-size: 14px;
  font-weight: bold;
  color: #00D7AF; }
  .business-main-container .text-link:hover {
    text-decoration: underline; }

.business-main-container .slider-container .slick-slider {
  width: calc(100vw);
  transform: translateX(-15px);
  overflow: hidden;
  padding-bottom: 28px; }
  .business-main-container .slider-container .slick-slider .slick-dots {
    position: relative;
    transform: translateY(-28px);
    z-index: 1; }

.business-main-container .why-container {
  margin-bottom: 50px !important; }
  @media screen and (max-width: 991px) {
    .business-main-container .why-container {
      margin-top: 100px !important;
      margin-bottom: 10px !important; } }
  .business-main-container .why-container .main-title {
    color: #F2F4FD; }
  .business-main-container .why-container .why-desc {
    max-width: 635px;
    margin: 20px auto 50px auto;
    text-align: center;
    font-size: 14px;
    font-weight: 600; }
  @media screen and (max-width: 991px) {
    .business-main-container .why-container .why-item {
      flex: none;
      max-width: none; } }
  .business-main-container .why-container .why-item .why-info {
    margin: auto;
    max-width: 200px;
    display: flex;
    flex-direction: column;
    align-items: center; }
    .business-main-container .why-container .why-item .why-info img {
      max-width: 160px;
      max-height: 160px; }
    .business-main-container .why-container .why-item .why-info h5 {
      margin: auto;
      font-weight: bold;
      font-size: 18px;
      text-align: center;
      color: #2C3652; }
    .business-main-container .why-container .why-item .why-info p {
      margin-top: 12px;
      font-weight: 600;
      font-size: 14px;
      text-align: center;
      color: #6E7F95; }
  @media screen and (max-width: 1023px) {
    .business-main-container .why-container {
      margin-top: 40px;
      margin-bottom: 20px; } }
