.checkout-v2-payment-modal {
  max-width: 458px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden; }
  .checkout-v2-payment-modal .modal-content {
    padding-left: 20px;
    padding-right: 20px; }
    .checkout-v2-payment-modal .modal-content .method-container {
      width: 100%;
      display: flex;
      align-items: center;
      margin-bottom: 30px; }
      .checkout-v2-payment-modal .modal-content .method-container .method-button {
        width: 143px;
        height: 48px;
        border-radius: 50px;
        transition: background-color 0.3s ease;
        color: #2C3652;
        font-weight: bold;
        font-size: 12px; }
        .checkout-v2-payment-modal .modal-content .method-container .method-button.selected {
          background-color: #E0EAFE; }
        .checkout-v2-payment-modal .modal-content .method-container .method-button:nth-of-type(1) {
          margin-right: 10px; }
    .checkout-v2-payment-modal .modal-content .pay-deposit-container {
      margin-top: 10px; }
    .checkout-v2-payment-modal .modal-content .price-container {
      margin-top: 10px;
      font-weight: bold;
      font-size: 14px;
      color: #2C3652; }
      .checkout-v2-payment-modal .modal-content .price-container span:nth-of-type(2) {
