      width: 100%; }
      .checkout-v2-payment-modal .modal-content .button-container .cc-button button,
      .checkout-v2-payment-modal .modal-content .button-container .bank-button button {
        height: 48px;
        min-width: 100% !important; }
        .checkout-v2-payment-modal .modal-content .button-container .cc-button button p,
        .checkout-v2-payment-modal .modal-content .button-container .bank-button button p {
