.checkout-v2-container .more-button img {
  margin-left: 12px;
  width: 10px;
  height: 7px;
  filter: brightness(0) saturate(100%) invert(50%) sepia(28%) saturate(292%) hue-rotate(174deg) brightness(92%) contrast(94%);
  transition: transform 0.3s ease; }

.checkout-v2-container .more-button .rotate-arrow {
  transform: rotate(180deg); }

.checkout-v2-container .checkout-collapse-more-container {
  margin-top: 0; }
  .checkout-v2-container .checkout-collapse-more-container .more-collapse-container {
    border: 1px solid #CADBEA;
    box-sizing: border-box;
    border-radius: 20px;
    padding: 20px;
    margin-bottom: 10px; }
    .checkout-v2-container .checkout-collapse-more-container .more-collapse-container .col-4:nth-of-type(2) .grey-text {
      justify-content: center; }
    .checkout-v2-container .checkout-collapse-more-container .more-collapse-container .col-4:nth-of-type(3) .grey-text {
      float: right; }
