.checkout-v2-container {
  margin-top: 30px;
  margin-bottom: 50px; }
  @media screen and (max-width: 991px) {
    .checkout-v2-container {
      margin-top: 0; } }
  .checkout-v2-container h2 {
    font-size: 24px;
    font-weight: bold;
    color: #2C3652; }
  .checkout-v2-container hr {
    background: #CADBEA;
    border-radius: 20px;
    margin: 30px 0; }
  .checkout-v2-container .grey-text {
    font-weight: 600;
    font-size: 12px;
    color: #B6BDCB;
    white-space: pre-wrap; }
  .checkout-v2-container .dark-text {
    font-weight: bold;
    font-size: 12px;
    color: #2C3652;
    white-space: pre-wrap; }
  .checkout-v2-container .green-text {
    font-weight: bold;
    font-size: 18px;
    color: #00D7AF;
    white-space: pre-wrap; }
  .checkout-v2-container .left-container {
    padding: 10px 40px 30px 40px;
    margin-bottom: 20px;
    margin-right: 40px; }
    @media screen and (max-width: 991px) {
      .checkout-v2-container .left-container {
        margin-right: 0; } }
  .checkout-v2-container .right-container {
    padding: 30px;
    margin-left: 0; }
    @media screen and (max-width: 991px) {
      .checkout-v2-container .right-container {
        margin-left: 0; } }
  .checkout-v2-container .left-container {
    border-radius: 20px;
    border: 1px solid #CADBEA; }
  .checkout-v2-container .right-container {
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05); }
  .checkout-v2-container .left-container,
  .checkout-v2-container .right-container {
    background: white;
    box-sizing: border-box; }
    @media screen and (max-width: 991px) {
      .checkout-v2-container .left-container,
      .checkout-v2-container .right-container {
        padding-left: 15px;
        padding-right: 15px; } }
  .checkout-v2-container hr {
    margin-top: 10px; }
  .checkout-v2-container .popup:before {
    background: linear-gradient(150.14deg, #6480EB 0.6%, #433BC4 100%);
    opacity: 0.9; }
  .checkout-v2-container .popup .checkout-button {
    min-width: 168px !important;
    margin: auto; }

.pay-deposit-container {
  background: #F9FAFF;
  border-radius: 20px;
  padding: 5px; }
  .pay-deposit-container p {
    font-weight: 600;
    font-size: 12px;
    text-align: center;
    margin: 0;
    color: #6E7F95; }
