.business-main-container .questions-container p {
  font-weight: 600;
  font-size: 14px;
  color: #6E7F95; }

.business-main-container .margin-padding {
  max-width: 1110px;
  padding: 0 40px;
  margin: auto; }
  @media screen and (max-width: 991px) {
    .business-main-container .margin-padding {
      padding: 0 15px; } }

.business-main-container .carousel-container {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media screen and (max-width: 991px) {
    .business-main-container .carousel-container {
      margin-bottom: 20px; } }
  .business-main-container .carousel-container .insurer-carousel-label {
    color: #2C3652;
    font-size: 14px;
    font-weight: bold;
    margin-bottom: -20px; }
  .business-main-container .carousel-container .insurer-logo-carousel {
    max-width: 650px;
    width: 100%;
    display: flex;
    justify-content: center;
    transform: translateY(-30px); }
    .business-main-container .carousel-container .insurer-logo-carousel .logo {
      height: 50px;
      display: flex; }
      .business-main-container .carousel-container .insurer-logo-carousel .logo img {
        margin: auto; }

.business-main-container .text-link {
  margin-left: 3px;
  font-size: 14px;
  font-weight: bold;
  color: #00D7AF; }
  .business-main-container .text-link:hover {
    text-decoration: underline; }

.business-main-container .slider-container .slick-slider {
