      height: 94px;
      transform: translateY(9px); }
      @media screen and (max-width: 420px) {
        .business-main-container .questions-container .whatsapp-container .small-whatsapp img {
          display: none; } }
  .business-main-container .questions-container .whatsapp-non-resp {
    display: none; }
    @media screen and (min-width: 428px) {
      .business-main-container .questions-container .whatsapp-non-resp {
        display: block; } }
  .business-main-container .questions-container .whatsapp-resp {
    display: block; }
    @media screen and (min-width: 428px) {
      .business-main-container .questions-container .whatsapp-resp {
        display: none; } }
  @media screen and (max-width: 767px) {
