.business-flow-container {
  background-color: #F9FAFF;
  min-height: calc(100vh - 60px);
  display: flex;
  flex-direction: column;
  overflow: auto; }
  @media screen and (min-width: 786px) {
    .business-flow-container {
      min-height: calc(100vh - 84px); } }
  .business-flow-container h3 {
    font-weight: bold;
    font-size: 14px;
    color: #2C3652;
    margin: 0; }
    @media screen and (min-width: 992px) {
      .business-flow-container h3 {
        font-size: 24px; } }
  .business-flow-container h6 {
    font-weight: normal;
    font-size: 12px;
    color: #2C3652;
    margin: 0; }
    @media screen and (min-width: 992px) {
      .business-flow-container h6 {
        font-weight: 600; } }
  .business-flow-container .business-flow-inner-container {
    flex: 1;
    margin: 0 auto;
    width: 1200px;
    max-width: 100%;
    padding: 0 15px;
    overflow: visible;
    min-height: calc(100vh - 120px - 221px); }
    @media screen and (min-width: 768px) {
      .business-flow-container .business-flow-inner-container {
        min-height: calc(100vh - 100px - 221px); } }
    .business-flow-container .business-flow-inner-container .flow-container {
      margin: 40px 0; }
      @media screen and (min-width: 768px) {
        .business-flow-container .business-flow-inner-container .flow-container {
          margin: 40px 20px; } }
      @media screen and (min-width: 1024px) {
        .business-flow-container .business-flow-inner-container .flow-container {
          margin: 40px; } }
    .business-flow-container .business-flow-inner-container .flow-back {
      display: flex;
      justify-content: flex-start;
      align-items: center; }
      .business-flow-container .business-flow-inner-container .flow-back button h6 {
        font-weight: bold; }
        @media screen and (min-width: 992px) {
          .business-flow-container .business-flow-inner-container .flow-back button h6 {
            font-weight: bold; } }
    .business-flow-container .business-flow-inner-container .flow-title {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      position: relative; }
      .business-flow-container .business-flow-inner-container .flow-title .title-img {
        position: absolute;
        transform: translateY(10px); }
        @media screen and (max-width: 991px) {
          .business-flow-container .business-flow-inner-container .flow-title .title-img {
            opacity: 0; } }
      .business-flow-container .business-flow-inner-container .flow-title h3 {
        z-index: 1; }
    .business-flow-container .business-flow-inner-container .flow-next {
      display: flex;
      justify-content: flex-end;
      align-items: center; }
      .business-flow-container .business-flow-inner-container .flow-next button {
        transform: translateY(-5px); }
        @media screen and (max-width: 991px) {
          .business-flow-container .business-flow-inner-container .flow-next button {
            min-width: 64px !important;
            min-height: 36px !important; }
            .business-flow-container .business-flow-inner-container .flow-next button p {
              font-size: 12px; } }
  .business-flow-container .top-container {
    padding: 70px 0 10px 0; }
    @media screen and (min-width: 992px) {
      .business-flow-container .top-container {
        padding: 50px 0 20px 0; } }
  .business-flow-container .sticky-top-container .flow-title {
    flex: 1;
    padding: 0; }
  .business-flow-container .sticky-top-container h3 {
    flex: 1;
    max-width: 300px; }
    @media screen and (min-width: 992px) {
      .business-flow-container .sticky-top-container h3 {
        transform: translateY(-5px);
        font-size: 18px; } }

.sticky-top-price {
  line-height: 1.8rem;
  margin-right: 40px; }
  @media screen and (max-width: 991px) {
    .sticky-top-price {
      display: none; } }
  .sticky-top-price p:nth-of-type(1) {
    font-weight: 600;
    font-size: 12px;
    text-align: right;
    color: #2C3652;
    margin: 0; }
  .sticky-top-price p:nth-of-type(2) {
    transform: translateY(-8px);
    font-weight: 800;
    font-size: 24px;
    text-align: right;
    color: #2C3652;
    margin: 0; }

.business-step-progress {
  background-color: #F9FAFF; }
  .business-step-progress .input-wrapper-main-new {
    background-color: #F9FAFF; }
  .business-step-progress .slider-container {
    width: 100vw;
    min-width: 100vw;
    transform: translateX(-15px); }
