      .business-main-container .get-coverage-container img {
        margin-top: 20px; }
      .business-main-container .get-coverage-container .left-container {
        padding-left: 0px;
        padding-right: 0px; }
      .business-main-container .get-coverage-container .input-wrapper-main-v3 {
