.checkout-v2-container .checkout-price-summary-container h2,
.checkout-v2-container .checkout-price-summary-container .dark-text,
.checkout-v2-container .checkout-price-summary-container .grey-text {
  display: flex;
  align-items: baseline; }
  .checkout-v2-container .checkout-price-summary-container h2 span:nth-of-type(1),
  .checkout-v2-container .checkout-price-summary-container .dark-text span:nth-of-type(1),
  .checkout-v2-container .checkout-price-summary-container .grey-text span:nth-of-type(1) {
    flex: 1;
    padding-right: 10px; }

.checkout-v2-container .checkout-price-summary-container .grey-text {
  color: #6E7F95;
  font-weight: normal; }

.checkout-v2-container .checkout-price-summary-container .sub_text {
  transform: translateY(-5px);
  color: #B6BDCB; }

.checkout-v2-container .checkout-price-summary-container .main {
  margin-bottom: 34px; }

.checkout-v2-container .checkout-price-summary-container .discount-label {
  margin-bottom: 18px; }
  @media screen and (min-width: 768px) {
    .checkout-v2-container .checkout-price-summary-container .discount-label {
      margin-bottom: 0; } }

@media screen and (min-width: 768px) {
  .checkout-v2-container .checkout-price-summary-container .discount-label-without-promo {
    position: absolute; } }

.checkout-v2-container .checkout-price-summary-container .price-item {
  display: flex;
  align-items: center;
  height: 100%; }
  .checkout-v2-container .checkout-price-summary-container .price-item .dark-text {
    flex: 1; }
    .checkout-v2-container .checkout-price-summary-container .price-item .dark-text span:nth-of-type(1) {
      margin-right: 10px; }
    .checkout-v2-container .checkout-price-summary-container .price-item .dark-text span:nth-of-type(2) {
      word-wrap: break-word;
      text-align: right; }

.checkout-v2-container .checkout-price-summary-container .total-container {
  padding: 10px 0; }

.checkout-v2-container .checkout-price-summary-container .big-value {
  font-size: 24px; }

.checkout-v2-container .checkout-price-summary-container .button-container {
  margin-top: 40px; }
  .checkout-v2-container .checkout-price-summary-container .button-container button {
    float: right; }

.checkout-v2-container .checkout-price-summary-container .left-price {
  padding-right: 10px; }

.checkout-v2-container .checkout-price-summary-container .input-wrapper-main-v3 {
  padding: 0; }

.checkout-v2-container .checkout-price-summary-container .promo-code-container {
  padding: 20px 0; }
  .checkout-v2-container .checkout-price-summary-container .promo-code-container .promo-code {
    display: flex;
    align-items: flex-end; }
    .checkout-v2-container .checkout-price-summary-container .promo-code-container .promo-code .input-wrapper-main-v3 {
      padding-right: 10px; }

.checkout-v2-container .checkout-price-summary-container hr {
  margin: 20px 0; }
