.business-main-container .margin-padding {
  max-width: 1110px;
  padding: 0 40px;
  margin: auto; }
  @media screen and (max-width: 991px) {
    .business-main-container .margin-padding {
      padding: 0 15px; } }

.business-main-container .carousel-container {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media screen and (max-width: 991px) {
    .business-main-container .carousel-container {
      margin-bottom: 20px; } }
  .business-main-container .carousel-container .insurer-carousel-label {
    color: #2C3652;
    font-size: 14px;
    font-weight: bold;
    margin-bottom: -20px; }
  .business-main-container .carousel-container .insurer-logo-carousel {
